.overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .9);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #fff;

  span {
    font-size: 30px;
    padding: 100px;
    background: url('./../images/loading-buffering.gif') no-repeat left center;
    margin: 0 0 20px;
  }

  i {font-style: normal; font-size: 13px;}
}
